@import url(https://fonts.googleapis.com/css?family=Open+Sans);
.goods{display:flex;flex-direction:column;justify-content:center;text-align:center;grid-gap:3px;gap:3px;list-style:none}

*{box-sizing:border-box}html{font-family:"Open Sans", sans-serif}body,h1,ul,li{margin:0;padding:0;border:0}.app{display:flex;flex-direction:column;text-align:center;padding:30px 60px;min-height:100vh;background-color:#181a1e}.app__title{text-align:center;margin-bottom:24px;color:#fff}.app__buttons{display:flex;justify-content:center;text-align:center;margin-bottom:24px}.app__button{display:flex;flex-direction:row;position:relative;padding:15px 25px;margin-left:15px;top:0;width:150px;color:#fff5f5;text-transform:uppercase;background-color:#181a1e;border:1px #ffffff26 solid;box-shadow:0 3px 10px #ffffff1a;transition:box-shadow 0.3s ease 0s, top 0.3s ease 0s, border 0.3s ease 0s, color 0.3s ease 0s}.app__button:first-child{margin-left:0}.app__button:hover{border-bottom:5px #ff2121 solid;box-shadow:0 6px 15px #ff1e1e80;border:1px #ff1e1e66 solid;top:-4px;color:#ff3232;cursor:pointer}

